export default {
	data() {
		return {
			cates: [],
			active: 0,
			secondData: []
		}
	},
	onLoad() {
		this.getPicsCate()
	},
	methods: {
		async getPicsCate() {
			const res = await this.$myRequest({
				url: "/api/getimgcategory"
			})
			this.cates = res.data.message;
			console.log(res)
			this.leftClickHandle(0, this.cates[0].id)
		},
		async leftClickHandle(index, id) {
			this.active = index;
			console.log(id)
			const res = await this.$myRequest({
				url: "/api/getimages/" + id
			})
			this.secondData = res.data.message;
			console.log(res)
		},
		priviewImg(current) {
			const urls = this.secondData.map((item) => {
				return item.img_url;
			})
			console.log(urls)
			uni.previewImage({
				urls: urls,
				current: current
			})
		}
	}
}
